/* eslint-disable multiline-ternary */
import {
  Fragment,
  useState,
  useEffect,
  useContext,
  useMemo,
  useCallback,
  forwardRef
} from "react"
import { Link } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { AbilityContext } from "@src/utility/context/Can"
import ModalAddEdit from "./modal"
import { neutralizeBack, revivalBack } from "@src/utility/Utils"
import {
  storeName,
  pageTitle,
  serverSideGrid,
  detailFromGrid,
  getApiAfterPut,
  getApiAfterPost,
  getApiAfterDelete,
  getSummarysData,
  getAlwaysGrid,
  getDataList,
  getDataById,
  setDataById,
  deleteData,
  deleteSelectedRowData
} from "./store"
import { getInvoice } from "../../invoices/store"
import { useDispatch, useSelector } from "react-redux"
import ReactPaginate from "react-paginate"
import DataTable from "react-data-table-component"
import debounce from "lodash.debounce"
import {
  ChevronDown,
  Share,
  Printer,
  FileText,
  File,
  Grid,
  Copy,
  Slack,
  User,
  Settings,
  Database,
  Edit2,
  MoreVertical,
  Trash2,
  Archive,
  Eye,
  CheckCircle,
  CheckSquare,
  Check,
  Square
} from "react-feather"

import { getUserData } from "@utils"

import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import toast from "react-hot-toast"
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  CardTitle,
  CardHeader,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  Badge,
  UncontrolledTooltip,
  CardText
} from "reactstrap"

import "@styles/react/libs/react-select/_react-select.scss"
import "@styles/react/libs/tables/react-dataTable-component.scss"
import Swal from "sweetalert2"
import withReactContent from "sweetalert2-react-content"
import { Skeleton } from "@mui/material"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"

const MySwal = withReactContent(Swal)

// ** Bootstrap Checkbox Component
const BootstrapCheckbox = forwardRef((props, ref) => (
  <div className="form-check">
    <Input type="checkbox" ref={ref} {...props} />
  </div>
))

// ** Table Header
const CustomHeader = ({
  store,
  toggleSidebar,
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm,
  selectedRow,
  handleDeleteSelected
}) => {
  const { t } = useTranslation()
  const ability = useContext(AbilityContext)
  const dispatch = useDispatch()
  const storeInvoice = useSelector((state) => state.invoices)
  const userData = getUserData()

  return (
    <div className="invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75">
      <Row>
        <Col xl="4" className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            <label htmlFor="rows-per-page">{t("Show")}</label>
            <Input
              className="mx-50"
              type="select"
              id="rows-per-page"
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: "5rem" }}
            >
              <option value="10">10</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </Input>
            <label htmlFor="rows-per-page">{t("Entries")}</label>
          </div>
        </Col>
        <Col
          xl="8"
          className="d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1"
        >
          {selectedRow?.length > 0 &&
          ability.can("delete", sessionStorage.getItem("current_page_perm")) ? (
            <div className="d-flex align-items-center mb-sm-0 mb-1 me-1">
              <Button
                className="delete-users"
                color="danger"
                onClick={handleDeleteSelected}
              >
                {t("Delete")} {selectedRow.length} {t(`Selected ${pageTitle}`)}
              </Button>
            </div>
          ) : null}
          <div className="d-flex align-items-center mb-sm-0 mb-1 me-1">
            <label className="mb-0" htmlFor="search-invoice">
              {t("Search")}:
            </label>
            <Input
              id="search-invoice"
              className="ms-50 w-100"
              type="text"
              value={searchTerm}
              onChange={(e) => handleFilter(e.target.value)}
            />
          </div>

          <div className="d-flex align-items-center table-header-actions">
            <UncontrolledDropdown className="me-1">
              <DropdownToggle color="secondary" caret outline>
                <Share className="font-small-4 me-50" />
                <span className="align-middle">{t("Export")}</span>
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem
                  className="w-100"
                  onClick={() =>
                    dispatch(
                      getInvoice({
                        rpt_name: "sample",
                        output_type: "pdf",
                        dataYear: userData.dataYear,
                        print: true,
                        as_attachment: true,
                        par_UNITKEY: 139,
                        par_SAMPLE: "ahh...ini adalah sample parameter"
                      })
                    )
                  }
                >
                  <Printer className="font-small-4 me-50" />
                  <span className="align-middle">{t("Print")}</span>
                </DropdownItem>
                <DropdownItem
                  className="w-100"
                  onClick={() =>
                    dispatch(
                      getInvoice({
                        rpt_name: "sample",
                        output_type: "pdf",
                        dataYear: userData.dataYear,
                        par_UNITKEY: 139,
                        par_SAMPLE: "ahh...ini adalah sample parameter"
                      })
                    )
                  }
                >
                  <File className="font-small-4 me-50" />
                  <span className="align-middle">PDF</span>
                </DropdownItem>

                <DropdownItem
                  className="w-100"
                  onClick={() =>
                    dispatch(
                      getInvoice({
                        rpt_name: "sample",
                        output_type: "xls",
                        dataYear: userData.dataYear,
                        as_attachment: true,
                        par_UNITKEY: 139,
                        par_SAMPLE: "ahh...ini adalah sample parameter"
                      })
                    )
                  }
                >
                  <Grid className="font-small-4 me-50" />
                  <span className="align-middle">Excel</span>
                </DropdownItem>

                <DropdownItem
                  className="w-100"
                  // onClick={() => downloadCSV(store.data)}
                  onClick={() =>
                    dispatch(
                      getInvoice({
                        rpt_name: "sample",
                        output_type: "csv",
                        dataYear: userData.dataYear,
                        as_attachment: true,
                        par_UNITKEY: 139,
                        par_SAMPLE: "ahh...ini adalah sample parameter"
                      })
                    )
                  }
                >
                  <FileText className="font-small-4 me-50" />
                  <span className="align-middle">CSV</span>
                </DropdownItem>

                {/* <DropdownItem className="w-100">
                  <Copy className="font-small-4 me-50" />
                  <span className="align-middle">Copy</span>
                </DropdownItem> */}
              </DropdownMenu>
            </UncontrolledDropdown>

            {ability.can(
              "post",
              sessionStorage.getItem("current_page_perm")
            ) ? (
              <Button
                className="add-new-user"
                color="primary"
                onClick={toggleSidebar}
                // onClick={handleAdd}
              >
                {t("Add New")} {t(pageTitle)}
              </Button>
            ) : null}
          </div>
        </Col>
      </Row>
    </div>
  )
}

const List = () => {
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])

  const { t } = useTranslation()
  const ability = useContext(AbilityContext)

  const { colors } = useContext(ThemeColors)
  const [sort_dir, setSortDir] = useState("desc")
  const [searchTerm, setSearchTerm] = useState("")
  const [currentPage, setCurrentPage] = useState(1)
  const [sort, setSort] = useState(null)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [selectedRows, setSelectedRows] = useState([])
  const [toggleCleared, setToggleCleared] = useState(false)

  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [loading, setLoading] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [showModal, setShowModal] = useState(false)
  const [formState, setFormState] = useState(null)

  const handleEdit = (rowData) => {
    setFormState("edit")
    if (!detailFromGrid) {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(getDataById(rowData.id)).then((res) => {
        if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        } else {
          setLoading(false)
          setShowModal(true)
        }
      })
    } else {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(setDataById(rowData)).then(() => {
        setLoading(false)
        setShowModal(true)
      })
    }
  }

  const handleCloseModal = () => {
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    revivalBack()
  }

  const toggleSidebar = () => {
    // console.log("here")
    setFormState("add")
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    neutralizeBack(handleCloseModal)
  }

  const handleDelete = async (id) => {
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You won't be able to revert")} ${t(pageTitle)}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${t(pageTitle)}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(`${t("Deleting")} Data ${t(pageTitle)}`)
      setLoading(true)
      dispatch(deleteData(id)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setLoading(false)
          toast.success(`${t("Successfully deleted")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  const handleDeleteSelected = async () => {
    const countSelectedCopy = selectedRows.length
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You will delete")} ${countSelectedCopy} ${t(
        "selected data. Deleted data cannot be recovered"
      )}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${countSelectedCopy} ${t(
        pageTitle
      )}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(
        `${t("Deleting")} ${countSelectedCopy} Data ${t(pageTitle)}`
      )
      setLoading(true)
      const values = selectedRows.map((a) => a.id)
      // console.log(values)
      dispatch(deleteSelectedRowData({ id: values })).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setSelectedRows([])
          setToggleCleared(!toggleCleared)
          setLoading(false)
          toast.success(
            `${t("Successfully deleted")} ${countSelectedCopy} ${t(pageTitle)}`,
            {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            }
          )
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  /////PAGINATION SERVERSIDE
  const handlePagination = (page) => {
    // console.log("handlePagination")
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        length: rowsPerPage,
        page: page.selected + 1
      })
    )
    setCurrentPage(page.selected + 1)
  }

  const handlePerPage = (e) => {
    // console.log("handlePerPage")
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        length: value,
        page: currentPage
      })
    )
    setRowsPerPage(value)
  }

  const handleDebounceFn = (inputValue) => {
    dispatch(
      getDataList({
        sort_dir,
        search: inputValue,
        sort,
        page: currentPage,
        length: rowsPerPage
      })
    )
  }

  const debounceFn = useCallback(debounce(handleDebounceFn, 500), [])

  const handleFilter = (event) => {
    // console.log("handleFilter")
    setSearchTerm(event)
    debounceFn(event)
  }

  const handleSort = (column, sortDirection) => {
    // console.log("handleSort")
    setSortDir(sortDirection)
    setSort(column.sortField)
    // console.log(column.sortField, sortDirection)
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage
      })
    )
  }

  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={""}
        nextLabel={""}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={"page-item"}
        nextLinkClassName={"page-link"}
        nextClassName={"page-item next"}
        previousClassName={"page-item prev"}
        previousLinkClassName={"page-link"}
        pageLinkClassName={"page-link"}
        containerClassName={
          "pagination react-paginate justify-content-end my-2 pe-1"
        }
      />
    )
  }
  /////PAGINATION SERVERSIDE

  const handleRowSelected = useCallback((state) => {
    setSelectedRows(state.selectedRows)
  }, [])

  const columns = [
    {
      name: t("Title"),
      center: false,
      sortable: true,
      // minWidth: "400px",
      sortField: "title",
      selector: (row) => row.title,
      cell: (row) => row.title
    },
    {
      name: t("Description"),
      center: false,
      sortable: true,
      // minWidth: "400px",
      sortField: "description",
      selector: (row) => row.description,
      cell: (row) => row.description
    },
    {
      name: t("Route Path"),
      center: false,
      sortable: true,
      // minWidth: "400px",
      sortField: "routePath",
      selector: (row) => row.routePath,
      cell: (row) => row.routePath
    },
    {
      name: t("Actions"),
      center: true,
      width: "140px",
      cell: (row) => (
        <div className="column-action text-center">
          {ability.can(
            "delete",
            sessionStorage.getItem("current_page_perm")
          ) ? (
            <>
              <Button.Ripple
                id="btn-delete"
                className="btn-icon rounded-circle"
                color="flat-danger"
                onClick={(e) => {
                  e.preventDefault()
                  handleDelete(row.id)
                }}
              >
                <Trash2 size={16} />
              </Button.Ripple>
              <UncontrolledTooltip placement="left" target="btn-delete">
                {t("Delete")}
              </UncontrolledTooltip>
            </>
          ) : null}

          {ability.can(
            "update",
            sessionStorage.getItem("current_page_perm")
          ) ? (
            <>
              <Button.Ripple
                className="btn-icon rounded-circle"
                color="flat-info"
                id="btn-edit"
                onClick={(e) => {
                  e.preventDefault()
                  handleEdit(row)
                }}
              >
                <Edit2 size={16} />
              </Button.Ripple>
              <UncontrolledTooltip placement="left" target="btn-edit">
                {t("Edit")}
              </UncontrolledTooltip>
            </>
          ) : null}
        </div>
      )
    }
  ]

  const tableColumns = useMemo(
    () =>
      store.isLoading
        ? columns.map((column) => ({
            ...column,
            sortable: false,
            cell: (row) => (
              <Skeleton
                sx={{ bgcolor: colors.dark.light }}
                animation="wave"
                className={column.name === "Actions" ? "ms-2" : ""}
                width={column.name === "Actions" ? 10 : "100%"}
                height={40}
              />
            )
          }))
        : columns.map((column) => ({
            ...column
          })),
    [store.isLoading, columns]
  )

  const dataToRender = () => {
    const filters = {
      search: searchTerm
    }
    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (!store.isLoading) {
      if (store.data.length > 0) {
        return store.data
      } else if (store.data.length === 0 && isFiltered) {
        return []
      } else {
        // console.log(store.allData)
        // return store.allData.slice(0, rowsPerPage)
      }
    } else {
      return Array(10).fill({})
    }
  }

  useEffect(() => {
    if (store.data.length === 0 || (!store.isLoading && getAlwaysGrid)) {
      setLoadingText(`${t("Loading Data")} ${t(pageTitle)}`)
      dispatch(
        getDataList({
          sort_dir,
          sort,
          search: searchTerm,
          page: currentPage,
          length: rowsPerPage
        })
      )
    }
  }, [dispatch, store.data.length, sort_dir, sort, currentPage])

  return (
    <Fragment>
      <UILoader blocking={loading} loader={<Loader />}>
        <Card className="overflow-hidden">
          <div className="react-dataTable react-dataTable-selectable-rows">
            <DataTable
              noHeader
              subHeader
              sortServer
              responsive
              pagination
              paginationServer
              paginationTotalRows={store.total}
              paginationComponent={CustomPagination}
              selectableRows
              onSelectedRowsChange={handleRowSelected}
              selectableRowsHighlight
              // onRowClicked={handleRowSelected}
              clearSelectedRows={toggleCleared}
              selectableRowsComponent={BootstrapCheckbox}
              noDataComponent={<NoDataDisplay />}
              // fixedHeader
              // fixedHeaderScrollHeight="600px"
              style={{ "min-height": "400px" }}
              highlightOnHover
              pointerOnHover
              columns={tableColumns}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className="react-dataTable"
              data={dataToRender()}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  toggleSidebar={toggleSidebar}
                  selectedRow={selectedRows}
                  handleDeleteSelected={handleDeleteSelected}
                />
              }
            />
          </div>
        </Card>

        {/* <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} /> */}
      </UILoader>

      {showModal && (
        <ModalAddEdit
          formState={formState}
          show={showModal}
          setShow={toggleSidebar}
          detailPage={false}
          setFormState={setFormState}
        />
      )}
    </Fragment>
  )
}

export default List
